* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root {
    --Lato: 'Lato', sans-serif;
    --Open_Sans: 'Open Sans', sans-serif;
}

.act_dark_blue {
    color: #4c76ff;
}

.active_blue {
    color: #00d8ff;
}

/* header_starting */

header {
    background: url('../img/StarWarsBg.jpg');
    width: 100%;
    height: 670px;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
}

/* header_nav */

header .header_nav {
    background-color: rgba(16, 22, 54, 50%);
}

header .navbar {
    padding: 0;
}

header .navbar-collapse {
    flex-grow: unset;
}

header .navbar .navbar-brand {
    color: #f0ff00;
    font-size: 20px;
    font-weight: 900;
    font-family: var(--Lato);
}

header .navbar .collapse .navbar-nav .nav-item .active {
    border-top: 1px solid #00d8ff;
}

header .navbar .collapse .navbar-nav .nav-item .nav-link {
    color: white;
}

/* center_words */

header .center_words {
    margin: auto;
    margin-top: 250px;
    text-align: center;
    font-family: var(--Lato);
}

header .center_words .title {
    font-weight: 900;
    font-size: 62px;
    color: white;
}

header .center_words p {
    font-size: 48px;
    font-weight: 100;
    color: white;
    margin-bottom: 0;
}

header .center_words .end_words {
    font-weight: 100;
    font-size: 18px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

header .center_words .end_words::before {
    content: "";
    display: block;
    height: 1px;
    width: 28px;
    background-color: #4c76ff;
    margin: 0px 40px 0 40px;
}

header .center_words .end_words::after {
    content: "";
    display: block;
    height: 1px;
    width: 28px;
    background-color: #4c76ff;
    margin: 0px 40px 0 40px;
}

header .center_words .social_apps {
    margin: auto;
    margin-top: 110px;
}

header .center_words .social_apps i {
    font-weight: 400;
    font-size: 22px;
    color: #6c6e71;
    margin: 0px 6px;
}

header .center_words .social_apps i:hover {
    color: white;
    transition: 300ms;
}

@media(max-width:991px) {
    header .center_words {
        margin-top: 160px;
    }

    .navbar-toggler-icon {
        width: 20px;
        height: 20px;
    }

    .navbar-toggler {
        padding: 4px 6px;
        line-height: 0;
    }
}

@media(max-width: 767px) {
    header .center_words p {
        font-size: 33px;
    }

    header .center_words .end_words {
        font-size: 13px;
    }
}

@media(max-width: 575px) {
    header .center_words .title {
        font-size: 44px;
    }

    header .center_words .end_words::before,
    header .center_words .end_words::after {
        display: none;
    }
}

/* section_starting */

/* section_features */

section .section_features {
    background: url('../img/features_bg.jpg');
    background-repeat: no-repeat;
    background-position: top;
